<template>
    <el-row>
        <el-col :span="6">
            <div class="grid-content bg-purple-dark">
                <el-input placeholder="请输入过滤条件" v-model="searchKey"></el-input>
            </div>
        </el-col>
        <el-col :span="18">
            <div class="grid-content bg-purple-dark" style="text-align: right">
                <el-button type="primary" @click="getVersions()">刷新数据</el-button>
                <el-button type="primary" @click="refreshVersion('','')">检测全部版本</el-button>
            </div>
        </el-col>
        <el-col :span="24">
            <div id="gather-data-form" class="grid-content bg-purple-dark">
                <el-table :data="filteredList" v-loading="loading" width="100%" align="right" :stripe="true" header-align="right" highlight-current-row >
                    <el-table-column type="index" width="50"></el-table-column>
                    <el-table-column prop="name" label="应用名称"></el-table-column>
                    <el-table-column prop="appversion" label="应用版本"></el-table-column>
                    <el-table-column prop="parent" label="parent"></el-table-column>
                    <el-table-column prop="date" label="检测时间"></el-table-column>
                </el-table>
            </div>
        </el-col>
        <el-col :span="24">
            <div class="grid-content bg-purple-dark">
                <el-dialog
                        title="版本信息"
                        :visible.sync="dialogVisible"
                        width="60%"
                        :before-close="handleClose">
                    <span>
                        <el-form ref="form" :model="form" label-width="100px">
                          <el-form-item label="应用名称:">
                            <el-input v-model="form.name"></el-input>
                          </el-form-item>
                            <el-form-item label="检测地址:">
                            <el-input v-model="form.gitUrl" ></el-input>
                           </el-form-item>
                        </el-form>
                    </span>
                    <span slot="footer" class="dialog-footer">
                        <el-button @click="dialogVisible = false">取 消</el-button>
                        <el-button type="primary" @click="editData">添加/更新</el-button>
                    </span>
                </el-dialog>
            </div>
        </el-col>
    </el-row>
</template>

<script>
    import Service from './service'
    export default {
      name: 'appManage',
      data () {
        return {
          searchKey: '',
          dialogVisible: false,
          dataList: [],
          loading: false,
          form: {
            name: '',
            gitUrl: ''
          }
        }
      },
      methods: {
        handleClose () {
          this.dialogVisible = false
        },
        handleEdit(index, row) {
          this.dialogVisible = true
          this.form.name = row.name
          this.form.gitUrl = row.gitUrl
          console.log(index, row)
        },
        refreshVersion(index, row) {
          this.loading = true
          this.form.name = row.name
          this.form.gitUrl = row.gitUrl
          console.log(index, row)
          Service.refreshVersion({name: this.form.name, gitUrl: this.form.gitUrl}).then(res => {
            this.$notify.success({
              title: '检测完成',
              content: `SUCC`
            })
            this.getVersions()
            this.loading = false
          }).catch(error => {
            console.log(error)
            this.loading = false
            this.$message.error('操作失败,请检查是否有此功能权限' + error.toString())
          })
        },
        editData() {
          Service.addVersionConfig({name: this.form.name, gitUrl: this.form.gitUrl}).then(res => {
            console.log(res)
            this.dialogVisible = false
            this.$notify.success({
              title: '修改成功',
              content: `SUCC`
            })
          }).catch(error => {
            this.loading = false
            this.$message.error('操作失败,请检查是否有此功能权限' + error.toString())
          })
        },
        getVersions() {
          Service.getVersionList().then((data) => {
            console.log(data.data)
            this.dataList = data.data
          }).catch(error => {
            console.log(error)
            this.$message.error('请求失败，请联系程序猿改BUG!' + error.toString())
          })
        }
      },
      mounted () {
        this.getVersions()
      },
      computed: {
        'filteredList' () {
          return this.dataList.filter(item => item.name.includes(this.searchKey)) || []
        }
      }
    }
</script>

<style scoped>

</style>


